import React from 'react';
import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const Fournisseur = () => {
    const navigate = useNavigate();
    const [textInput, setTextInput] = useState('');
    const [responseMessage, setResponseMessage] = useState('');
    const [nom, setNom] = useState('');
    const [adresse, setAdresse] = useState('');
    const [contact, setContact] = useState('');
    const [error, setError] = useState('');
    const [listeFournisseurs, setListeFournisseurs] = useState([]);
    const [isFormVisible, setFormVisible] = useState(false);
    const [btnlabel, setBtnlabel] = useState('+');

    const handleNomChange = (e) => {
        setNom(e.target.value);
    };

    const handleAdresseChange = (e) => {
        setAdresse(e.target.value);
    };

    const handleContactChange = (e) => {
        setContact(e.target.value);
    };

    const handleTextChange = (event) => {
        setTextInput(event.target.value);
    };

    useEffect(() => {
        const fetchData = async () => {
        try {
            //const response = await axios.get('http://localhost:5000/afficher_fournisseur');
            const response = await axios.get('http://34.234.90.158:443/afficher_fournisseur', {
                withCredentials: true,
              });
            setListeFournisseurs(response.data.fournisseurs);
            console.log(response.data.fournisseurs)
        } catch (error) {
            console.error('Erreur lors de la rcupration de la liste des fournisseurs:', error);
        }
    };
        fetchData();
    }, []);
    
    const toggleForm = () => {
        setFormVisible(!isFormVisible);
        setBtnlabel(isFormVisible ? '+' : '-');
    };

    const onSubmit = async (e) => {
        e.preventDefault();
        try {
            //const response = await axios.post('http://localhost:5000/add_fournisseur', {
            const response = await axios.post('http://34.234.90.158:443/add_fournisseur', {
            nom: nom,
            adresse: adresse,
            contact: contact,
            }, {
            withCredentials: true,
          });
          console.log(nom,adresse,contact);
          if (response.status === 201) {
            setAdresse("")
            setContact("")
            setNom("")
            navigate('/dashbord');
            console.log(response.data);
          }
        } catch (error) {
            console.error('Error during adding:', error);
            setError('Information incorrecte.');
        }
    };
    
    const sendDataFournisseur = async (e) => {
        e.preventDefault();
        if (textInput.trim() !== '') {
            try {
                // Envoyez le texte au backend
                // Remplacez le lien ci-dessous par votre URL backend
                const response = await axios.post('http://34.234.90.158:443/add_stock', { text: textInput }, { withCredentials: true });
    
                const messages = response.data.message;
                if (messages) {
                    console.log('Rponse du serveur:', messages);
                    console.log('------');
                    console.log(messages.length);
                    if (messages.length === 0) {
                        setTextInput("");
                        navigate("/dashbord");
                    } else {
                        navigate("/manageFournisseur");
                        setResponseMessage(messages);
                    }
                } else {
                    console.log('Insertion russie!');
                }
            } catch (error) {
                console.error('Erreur lors de la requte au serveur:', error);
    
                // Mettez  jour le message de rponse en cas d'erreur
                setResponseMessage('Une erreur s\'est produite lors de la requte au serveur.');
            }
        } else {
            // Mettez  jour le message de rponse si le champ de texte est vide
            setResponseMessage('Veuillez saisir du texte dans le champ ci-dessus.');
        }
    };
    

    return (
        <div className="container"><br/>
            <div className='row'>
                <div className="col-md-6 col-12">
                    <h3>Ajouter un nouveau Fournisseur</h3><br/>
                    <form onSubmit={onSubmit}>
                        <div className="mb-3">
                            <label className="form-label"><strong>Nom</strong></label>
                            <input type="text" className="form-control w-50"
                            value={nom}
                            onChange={handleNomChange}
                            required/>
                        </div>
                        <div className="mb-3">
                            <label className="form-label"><strong>Adresse</strong></label>
                            <input type="text" className="form-control w-50"
                            value={adresse}
                            onChange={handleAdresseChange}
                            required/>
                        </div>
                        <div className="mb-3">
                            <label className="form-label"><strong>Contact</strong></label>
                            <input type="text" className="form-control w-50"
                            value={contact}
                            onChange={handleContactChange}
                            required/>
                        </div>
                        <div className="mb-3">
                            {error && <p className="text-danger">{error}</p>}
                        </div>
                        <button type="submit" className="btn btn-primary">Ajout</button>
                    </form><br/>
                </div>
                <div className="col-md-4 col-12">
                    <h3>Enregistrer des commandes</h3><br/>
                    <button onClick={toggleForm} className="btn btn-info"><strong>{btnlabel}</strong></button><br/>
                        {isFormVisible && (
                            <div>
                            <form onSubmit={sendDataFournisseur}>
                                <div className="form-group">
                                <label htmlFor="exampleTextarea"></label>
                                <textarea className="form-control" id="exampleTextarea" rows="4"
                                value={textInput} 
                                onChange={handleTextChange}/><br/>
                                </div>
                                <button type="submit" className="btn btn-primary">Submit</button>
                            </form>
                            <div className="mb-3">
                                    {Array.isArray(responseMessage) && <div >{responseMessage.map((message, index) => (
                                    <li key={index} className="text-danger">{message.message}</li>
                                ))}</div>}
                                </div>
                            </div>
                        )}
                    </div>
                </div>
                <div className='row mt-2'>
                    <h1>Liste des Fournisseurs</h1>
                    <div className="row mt-2">
                        {listeFournisseurs.map(OneFournisseur => (
                        <div key={OneFournisseur.ID_Fournisseur} className="col-sm-12 col-md-6 col-lg-4">
                            <div className="card mb-5" id="divCard" 
                            onMouseEnter={e => e.currentTarget.style.border = 'solid 4px #008080'}
                            onMouseLeave={e => e.currentTarget.style.border = 'solid 4px #f5f5f5'}
                            >
                            <h5 className="card-title"><strong>Nom : </strong>{OneFournisseur.Nom_Fournisseur} </h5>
                            <p className="card-text"><strong>Adresse: </strong>{OneFournisseur.Adresse} </p>
                            <p className="card-text"><strong>Contact: </strong>{OneFournisseur.Contact} </p>
                            <p className="card-text d-none">id: {OneFournisseur.ID_Fournisseur}</p>
                            <div id="divEdit">
                                <Link id="idEdit" to={"/edit/" + OneFournisseur.ID_Fournisseur}>Edit</Link>
                            </div>
                            </div>
                        </div>
                        ))}
                    </div>
                </div>
            </div>
    );
};

export default Fournisseur;